import Vue from 'vue'
import VueRouter from 'vue-router'
import AdminLogin from "../admin/AdminLogin";
import Layout from '../admin/Layout';
import Category from '../admin/Category'
import Product from "../admin/Product";
import User from "../admin/User";
import Order from "../admin/Order";
import Home from  "../fore/Home"
import Register from "../fore/Register";
import Login from "../fore/Login"
import CategoryPage from '../fore/CategoryPage'
import Container from "../fore/Container";
import ProductPage from "../fore/ProductPage";
import SearchPage from "../fore/SearchPage";
import Confirm from "../fore/Confirm";
import Cart from "../fore/Cart";
import Pay from "../fore/Pay";

Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    name:'',
    component:Container,
    children:[
      {
        path:'/',
        name: '',
        component: Home
      },
      {
        path:'/category',
        name:'',
        component:CategoryPage
      },
      {
        path:'/product',
        name:'',
        component:ProductPage
      },
      {
        path:'/search',
        name:'',
        component:SearchPage,
      },
    ]
  },
  {
    path:'/login',
    name:'',
    component:Login
  },
  {
    path:'/register',
    name:'',
    component:Register
  },
  {
    path:'/confirm',
    name:'',
    component:Confirm,
  },
  {
    path:'/cart',
    name:'',
    component:Cart,
  },
  {
    path:'/pay',
    name:'',
    component:Pay,
  },
  {
    path:'/admin/login',
    name:'admin',
    component:AdminLogin
  },
  {
    path: '/admin',
    name: '',
    component: Layout,
    children: [
      {
        path: '/admin',
        name: '',
        component: Category
      },
      {
        path: '/admin/product',
        name: '',
        component: Product
      },
      {
        path: '/admin/order',
        name: '',
        component: Order
      },
      {
        path: '/admin/user',
        name: '',
        component: User
      },
    ]
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../admin/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
